#contact {  
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  justify-content: center;
  height: 600px;
  max-width: 1200px;
  margin: 0 auto;
  box-shadow: 0px 1px 21px 8px rgba(12,25,96,0.19);
  @include for-big-mobiles {
    grid-template-columns: 2fr 3fr;
  }

  & .box1 {
    background: #0C3A60;
    padding: 60px;

    & h3 {
      font-size: 32px;
      color: #00a1d6;
      margin-bottom: 10px;
    }
  }
  & .box2 {
    background: #fff;
  }
}